<template>
  <div class="home">
    <img :src="chara" width="140"><br>
    <div class="balloon1">
      <textarea v-model="msg" rows="5" cols="80" placeholder="wirte something to here"></textarea>
    </div><br>
    <p @click="sendMessage" >Send Message</p>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios'
const querystring = require('querystring');

export default {
  name: 'home',
  data () {
    return {
      msg: "",
      chara: require("../assets/josei_02_a.png")
    }
  },
  methods: {
    sendMessage () {
      var config = {
          headers: {
              'Content-Type': 'text/plain'
          },
        responseType: 'text'
      };
      axios.post('/oneline',  this.msg, config)
      .then(res => {
        axios.post('/api', { contents: res.data, flag: 0, flag2: 0 })
        .then(res => {
          var  num = Number(res.data)
          console.log(num)
          if(num == 0){
            num = parseInt(Math.random() * 2)
            if(num == 0){
              this.chara = require("../assets/josei_02_a.png")
            } else {
              this.chara = require("../assets/josei_02_k.png")
            }
          } else if(num >= 1 && num < 5){
            this.chara = require("../assets/josei_02_b.png")
          } else if(num >= 5 && num < 10){
            this.chara = require("../assets/josei_02_e.png")
          } else if(num >= 10){
            this.chara = require("../assets/josei_02_f.png")
          } else if(num <= -1 && num > -5){
            this.chara = require("../assets/josei_02_g.png")//huan
          } else if(num <= -5&& num > -10){
            this.chara = require("../assets/josei_02_h.png") //sagesumu
          } else if(num <= -10 && num > -15){
            this.chara = require("../assets/josei_02_c.png") //oko
          }  else if(num <= -15){
            this.chara = require("../assets/josei_02_d.png")//oko kutiake
          }
        }).catch(error => {
          console.log(error)
        },)
      }).catch(error => {
        console.log(error)
      },)
    }
  }
}
/* eslint-disable */
</script>

